.projects {
    background-color: rgb(182, 139, 88);
    padding: 40px 0;
}

.projectsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    text-align: center;
    background-color: #6da66d;
}

.projectsBlock {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}